<template>
  <div id="change-group-name">
    <h3>
      <van-icon
        name="arrow-left"
        style="width: 40px"
        color="rgb(107, 107, 107)"
        @click="routerGoBack"
      />
    </h3>
    <div class="title">
      <p>修改群聊名称</p>
      <p>修改群聊名称,将在群内通知其他成员</p>
    </div>
    <div class="change-groud">
      <img src="../../assets/image/images/groupImg.webp" />
      <input type="text" v-model="inputValue" autofocus />
    </div>
    <p><button @click="setGroupName">完成</button></p>
  </div>
</template>

<script>
import WebIM from "../../utils/WebIM";
import { mapState } from "vuex";
export default {
  data() {
    return {
      inputValue: "",
      groudId: "",
    };
  },
  created() {
    this.getGroupId();
  },
  watch: {
    userId() {
      this.getGroupId();
    },
  },
  computed: {
    ...mapState({
      newCreateGroupId: (state) => state.home.newCreateGroupId,
      cacheClickGroupId: (state) => state.home.cacheClickGroupId,
      userId: (state) => state.user.userId,
    }),
  },
  methods: {
    routerGoBack() {
      this.$router.go(-1);
    },
    getGroupId() {
      if (this.userId !== "") {
        if (this.newCreateGroupId !== "") {
          this.groupId = this.newCreateGroupId;
        } else if (
          this.newCreateGroupId == "" &&
          this.cacheClickGroupId !== ""
        ) {
          this.groupId = this.cacheClickGroupId;
        } else if (
          this.newCreateGroupId == "" &&
          this.cacheClickGroupId == ""
        ) {
          this.groupId = sessionStorage.getItem("groupInfoCacheSave");
        }
        this.getGroupName();
      }
    },
    getGroupName() {
      let option = {
        groupId: this.groupId,
      };
      WebIM.conn.getGroupInfo(option).then((res) => {
        this.inputValue = res.data[0].name;
      });
    },
    setGroupName() {
      let option = {
        groupId: this.groupId,
        groupName: this.inputValue,
        description: "A description of group",
      };
      WebIM.conn.modifyGroup(option).then((res) => console.log(res));
    },
  },
};
</script>

<style lang="scss" scoped>
#change-group-name {
  width: 100vw;
  height: 100vh;
  color: rgb(164, 164, 164);
  background-color: var(--theme-background-black);
  h3 {
    width: 100%;
    height: 45px;
    padding: 10px 10px 0 10px;
    background-color: rgb(67 67 67);
  }
  .title {
    margin-top: 45px;
    text-align: center;
    p {
      &:first-child {
        font-size: 18px;
      }
    }
  }
  .change-groud {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 20px 30px;
    img {
      width: 40px;
      height: 40px;
      margin-right: 10px;
    }
    input {
      flex: 1;
      padding: 8px;
      border: 0px solid #000;
      outline: 0px solid #000;
      background-color: rgb(67 67 67);
    }
  }
  p {
    text-align: center;
    button {
      width: 50%;
      padding: 15px;
      border-radius: 8px;
      border: 0px;
      background-color: var(--theme-green);
      color: #fff;
      font-size: 15px;
    }
  }
}
</style>